import bottomNav from '@/common/mixins/bottom-nav.js'
export default {
	mixins:[bottomNav],
	data() {
		return {
			title: 'Hello'
		}
	},
	onLoad() {
		this.initUserInfo()
		
	},
	
	methods: {
		initUserInfo(){
			try {
				const value = uni.getStorageSync('chat_user_info');
				if (value.id) {
					let {id,nickname,figureurl}=value
					this.$store.commit('setChatUserInfo',{id,nickname,figureurl})
				}
			} catch (e) {}
		}
	}
}
